import React, { Component } from 'react';
import { SwipeAction, List } from 'antd-mobile';
import SwipeableViews from 'react-swipeable-views';

import './index.less'

 class Home extends Component {
     constructor(props) {
         super(props);
         this.state = {
             id:localStorage.getItem('capter_id'),
             title:'',
             capter:[],//将章节分页
             paragraph:[],//将文本内容分成段落
             text:''
         }
         this.styles = {
             slide: {
                 padding: 15,
                 minHeight: 100,
                 color: '#fff',
             },
             slide1: {
                 background: '#FEA900',
             },
             slide2: {
                 background: '#B3DC4A',
             },
             slide3: {
                 background: '#6AC0FF',
             },
         };

     }
     getData(type){
        var id =localStorage.getItem('capter_id')
        if(id){

        }else{
            id=1
        }
        if(type=='+'){
            id = parseInt(id)+1
        }if(type=='-'){
            id= parseInt(id)-1<1?1: parseInt(id)-1
        }

            fetch('http://www.zhufangzhou.com:3000/booktext?id='+id)
                .then((response)=>{
                    if(response.status===200){
                        response.json().then((respData)=>{
                            var text = respData.data.content;
                            var title =  respData.data.title
                            var capterNum = 444;//每页最大字数
                            var length = Math.ceil(text.length/capterNum)
                            var index = 0;
                            var capter
                            var arr =[]
                            for(let i = 1;i<=length;i++){
                                capter = text.substr(index,capterNum);
                                arr.push({"capter":capter,"title":title})
                                index = capterNum*i
                            }
                            var newcapter = this.state.capter.concat(arr)
                            this.setState({capter:newcapter})
                            localStorage.setItem('capter_id',id);
                            this.setState({id: id});
                        })
                    }
                })
                .catch((error)=>{
                    console.log(error);
                })
    }
      initParagraph(data){//处理段落
          var paragraph = data.split('    ');
          return paragraph
      }
     onChangeIndex(index,indexLatest){
         if(index==this.state.capter.length){
                 this.getData('+')
             }if(index<0){
             this.getData('-')
         }

     }
     onSwitching(index,type){
         if(index==this.state.capter.length-1){
             this.getData('+')
         }
         console.log(index,type)
     }
     componentWillMount(){
        var id
        if(localStorage.getItem('capter_id')){
           id =localStorage.getItem('capter_id')
        }else{
            id=1
        }
         this.getData(id)
     }
    render(){
        return (
            <div className="book" onClick={()=>{}}>
                <SwipeableViews onSwitching={this.onSwitching.bind(this)}>
                    {
                        this.state.capter.map((item,index)=>{
                        return (
                            <div className="text" key={index}>
                                <h2 className="title">
                                    {item.title}
                                </h2>
                                {this.initParagraph(item.capter).map((ele,index)=>{
                                    return(
                                        <p className="paragraph" key={index}>{ele}</p>
                                    )
                                })}
                            </div>
                        )
                    })
                    }
                </SwipeableViews>

              {/*<div className="bottom_Nav">*/}
                  {/*<li onClick={()=>this.getData('-')}>上一页</li>*/}
                  {/*<li>目录</li>*/}
                  {/*<li onClick={()=>this.getData('+')}>下一页</li>*/}
              {/*</div>*/}
            </div>
        )
    }
}
export default Home